
import { mapGetters } from 'vuex';
import Vue from 'vue'
import User from '@/types/user'

export default Vue.extend({
  computed: {
    ...mapGetters(['users'])
  },
  methods: {
    profile(user: User) {
      this.$store.commit('update_user', user);
      this.$router.push(`/user/${user.id}`)
    }
  },
  mounted() {
    this.$store.dispatch('getUsers');
  }
})
